<template>
  <div class="maps-container">
   
   
    <div v-if="view" class="box">
      <div id="province" style="width:100%; height:100%"></div>
    </div>
    <div v-if="!view" class="box">
      <div id="city" style="width:100%; height:100%"></div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      listArr: [], //城市json
      max: "", //最大value值
      min: "", // 最小value值
      view: true ,/* 切换省份地图 州市地区 */
      ynCodeList:[530000,530100, 530300, 530400, 530500, 530600, 530700, 530800, 530900, 532300, 532500, 532600, 532800, 532900, 533100, 533300, 53340]
    };
  },
  created() {
    this.getCityJson(530000, "云南省");
  },
  mounted() {},
  methods: {
    getCityJson(code, city) {
      if(this.ynCodeList.indexOf(code) == -1){
        console.log("阻止请求")
           return;
      }
     
      /* 根据编码获取json */
      let parms = "/yunnan/" + code + ".geo.json";
      axios.get(parms).then(res => {
        console.log(res.data);
        this.getCityData(res.data);
        setTimeout(() => {
          console.log(this.view);
          if (this.view) {
            this.drawProvinceMap(city, res.data);
          } else {
            this.drawCityMap(city, res.data);
          }
        });
      });
    },
    getCityData(obj) {
      // 获取城市名称数据
      if (obj) {
        let arr = obj.features;
        // 循环取出 城市名称和value值
        for (var j = 0; j < arr.length; j++) {
          this.max = arr[0].properties.adcode;
          this.min = arr[0].properties.adcode;
          if (arr[j].properties.adcode > this.max) {
            this.max = arr[j].properties.adcode;
          }
          if (arr[j].properties.adcode < this.min) {
            this.min = arr[j].properties.adcode;
          }
          this.listArr.push({
            name: arr[j].properties.name,
            value: arr[j].properties.adcode
          });
        }
      }
    },
    drawProvinceMap(mapName, obj) {
      let _this = this;
      let echartsMap = this.$echarts.init(document.getElementById("province"));

      this.$echarts.registerMap(mapName, obj); 
      echartsMap.setOption({
        visualMap: {
          min: _this.min,
          max: _this.max,
          show: false,
          inRange: {
            color: ["lightskyblue", "yellow", "orangered"]
          }
        },
        series: [
          {
            type: "map",
            map: "",
            mapType: mapName,
            itemStyle: {
              normal: { label: { show: true } },
              emphasis: { label: { show: true } },
              emphasis: {
                areaColor: "#67C23A" //鼠标进入时的颜色
              }
            },
            data: _this.listArr
          }
        ],
        tooltip: {
          trigger: "item",
          formatter(params) {
            const item = params;
            return `
                ${item.data.name} : ${item.data.value}
               `;
          }
        }
      });
      echartsMap.on("click", this.echartsMapClick);
    },
    echartsMapClick(params) {
      console.log("地图点击", params);
      /* 省级地图点击 */
      this.view = false;
      this.getCityJson(params.value, params.name);
    },
    drawCityMap(mapName, obj) {
      let _this = this;
      let echartsCityMap = this.$echarts.init(document.getElementById("city"));
      this.$echarts.registerMap(mapName, obj);
     
      echartsCityMap.setOption({
        visualMap: {
          min: _this.min,
          max: _this.max,
          show: false,
          inRange: {
            color: ["lightskyblue", "yellow", "orangered"]
          }
        },
        series: [
          {
            type: "map",
            map: "",
            mapType: mapName,
            itemStyle: {
              normal: { label: { show: true } },
              emphasis: { label: { show: true } },
              emphasis: {
                areaColor: "#67C23A" //鼠标进入时的颜色
              }
            },
            data: _this.listArr
          }
        ],
        tooltip: {
          trigger: "item",
          formatter(params) {
            const item = params;
            return `
                ${item.data.name} : ${item.data.value}
               `;
          }
        },
      });
    }
  }
};
</script>
<style scoped >
.maps-container {
  width: 100%;
  height: 100vh;
  background: #044161;
}
.box {
  width: 100%;
  height: 100vh;
}

</style>
